import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import TitleBar from 'components/topbar'
import connect from 'connect'
import { SearchBar, Button, WhiteSpace, WingBlank, Carousel, List, Grid, Card, Tabs } from 'antd-mobile';
const data1 = Array.from(new Array(4)).map(() => ({
  icon: require('../../images/tj11.png'),
  icon1: require('../../images/tj11.png'),
}));


@connect

class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
           disabled: false,
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       
    }


      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }


    render () {
      const tabs = [
        { title: '全部' },
        { title: '待付款' },
        { title: '待发货' },
        { title: '待收货' },
        { title: '待评价' },
      ];
      const data = [
        {
          img: 'https://zos.alipayobjects.com/rmsportal/dKbkpPXKfvZzWCM.png',
          title: 'Meet hotel',
          des: '商品名称商品名称商品名称商品名称商品名称商品名称商品名称.',
          value: 0,
        },
      ];
        const Item = List.Item;
        const Brief = Item.Brief;
        return (
            <div className="myorder-container">
            <TitleBar title="确认订单"/>
            
            <List  className="my-list">
              <Item arrow="horizontal" align="middle"  thumb={require('../../images/dd_dz.png')} multipleLine>
                收货人：翟阳春    <span style={{textAlign: 'right',marginLeft: '0.5rem'}}>18224528561</span><Brief><div  className="address">收货地址：河南省郑州市金水区北林路街道办事处经
              三名筑5栋11楼127室</div></Brief>
              </Item>
            
            {data.map((obj, index) => (
             <div  key={'myordercard' + index} >
              <Card full={true}>
                    <Card.Header
                      thumb={require('../../images/lian.png')}
                      thumbStyle={{width: '1rem',height: '0.75rem', borderRadius: '0.03rem'}}
                      extra={
                       <div style={{ }}>
                         <div className="destext">{obj.des}</div>
                         <div className="mid"></div>
                         <div><span className="jiatext">¥9900.00</span><span className="numtextdes">x1</span></div>
                       </div>
                      }
                    />
                    <Card.Body>
                      <div className="cardbody">合计：￥9900.00（含运费￥1.00）</div>
                    </Card.Body>
              </Card>
           </div>
            ))}
            <Item extra={<img src={require('../../images/xuanzhong.png')} alt="LOGO" className="xuanzhong"  />}  thumb={require('../../images/weixin.png')} multipleLine>
              微信支付
            </Item>
            </List>
            <div className="cardfooter">
                   <Button type="warning"  style={{ marginRight: '4px' }} className="am-button-borderfix">确认支付（￥9900.00）</Button>
            </div>
     </div>
        )
    }
}
export default Home


